	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"Summary","depth":1},{"value":"The Problem","depth":2},{"value":"Strategy","depth":2},{"value":"Deliverables","depth":2},{"value":"Conclusion","depth":1}],"html":"<h1>Summary</h1>\n<p>To prepare independent York University students who are away from their families on extreme weather procedures, I have devised an advertising campaign that utilizes the university’s existing resources to effectively bring the community together in times of weather emergencies.</p>\n<h2>The Problem</h2>\n<p>Due to global warming and climate change, extreme weather in Toronto and other cities is now a reality. In Toronto, extreme weather has been witnessed thr</p>\n<p>ough recent disasters such as the ice storm of 2013, gradually increasing temperatures, and heavy rainfall. Extreme weather is already affecting communities locally and globally—therefore, it is imperative that individuals, organizations, and the government are educated on the matter, and that they know the importance of community resilience.</p>\n<h2>Strategy</h2>\n<p>Working with CREW (Community Resilience to Extreme Weather), the goal of this project is to ultimately generate discussion about extreme weather preparedness and community resilience within the York U community. With an audience of mainly young adults, targeting them on social media would be the most effective way to grab their attention.</p>\n<p>The campaign will make use of existing university resources and means of communication to effectively bring the community together. Through these means the campaign will be able to promote community resilience and trust, self-sufficiency, as well as educate the target audience on the issues.</p>\n<h2>Deliverables</h2>\n<p>In my sketching process, the use of the “U” as the main symbol for the campaign emerged. Not only does the “U” represent the word “Unity”—which is the main goal of this campaign—it also ties in with York University’s existing university branding. Thus, through my usage of the symbol “U”, I’ve created a campaign that’s specific to the target audience and message.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 150.10706638115633%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"YorkUnite poster\"\n        title=\"\"\n        src=\"/static/yunite_poster-8ac18feb1062914e862582d0d8b75f7a-bc86b.png\"\n        srcset=\"/static/yunite_poster-8ac18feb1062914e862582d0d8b75f7a-c3a8a.png 125w,\n/static/yunite_poster-8ac18feb1062914e862582d0d8b75f7a-54de6.png 250w,\n/static/yunite_poster-8ac18feb1062914e862582d0d8b75f7a-bc86b.png 500w,\n/static/yunite_poster-8ac18feb1062914e862582d0d8b75f7a-da458.png 750w,\n/static/yunite_poster-8ac18feb1062914e862582d0d8b75f7a-72bd8.png 934w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>The process was a series of back-and-forth between me, the client (CREW), and my class. Once the brand was finalized, I then implemented it across different media.</p>\n<p>One of the deliverables was a postcard. CREW uses these cards as another form of advertising for their cause. The postcard I designed not only serves as advertising, but it also serves as a checklist for the community to use when preparing their emergency kits.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"YorkUnite postcards\"\n        title=\"\"\n        src=\"/static/postcards-13332161b6e231c9a1fc0f684e62b724-755ae.jpg\"\n        srcset=\"/static/postcards-13332161b6e231c9a1fc0f684e62b724-f07fc.jpg 125w,\n/static/postcards-13332161b6e231c9a1fc0f684e62b724-9d06f.jpg 250w,\n/static/postcards-13332161b6e231c9a1fc0f684e62b724-755ae.jpg 500w,\n/static/postcards-13332161b6e231c9a1fc0f684e62b724-63ca3.jpg 750w,\n/static/postcards-13332161b6e231c9a1fc0f684e62b724-9b9a3.jpg 1000w,\n/static/postcards-13332161b6e231c9a1fc0f684e62b724-6e031.jpg 1500w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>The main medium for delivering content will be social media. Thus, I adapted the brand to different digital ads and banners that could be used by the university as digital advertising.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 77.77777777777777%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"YorkUnite digital ad\"\n        title=\"\"\n        src=\"/static/york_page_mockup_mock-96d2539248cd548f5e33b11d046e119f-755ae.jpg\"\n        srcset=\"/static/york_page_mockup_mock-96d2539248cd548f5e33b11d046e119f-f07fc.jpg 125w,\n/static/york_page_mockup_mock-96d2539248cd548f5e33b11d046e119f-9d06f.jpg 250w,\n/static/york_page_mockup_mock-96d2539248cd548f5e33b11d046e119f-755ae.jpg 500w,\n/static/york_page_mockup_mock-96d2539248cd548f5e33b11d046e119f-63ca3.jpg 750w,\n/static/york_page_mockup_mock-96d2539248cd548f5e33b11d046e119f-9b9a3.jpg 1000w,\n/static/york_page_mockup_mock-96d2539248cd548f5e33b11d046e119f-6e031.jpg 1500w,\n/static/york_page_mockup_mock-96d2539248cd548f5e33b11d046e119f-7b98f.jpg 2250w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 77.77777777777777%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"YorkUnite FB\"\n        title=\"\"\n        src=\"/static/fbdigitalads_mockup-51f8b282fbb3df95731d171247ebd4f0-755ae.jpg\"\n        srcset=\"/static/fbdigitalads_mockup-51f8b282fbb3df95731d171247ebd4f0-f07fc.jpg 125w,\n/static/fbdigitalads_mockup-51f8b282fbb3df95731d171247ebd4f0-9d06f.jpg 250w,\n/static/fbdigitalads_mockup-51f8b282fbb3df95731d171247ebd4f0-755ae.jpg 500w,\n/static/fbdigitalads_mockup-51f8b282fbb3df95731d171247ebd4f0-63ca3.jpg 750w,\n/static/fbdigitalads_mockup-51f8b282fbb3df95731d171247ebd4f0-9b9a3.jpg 1000w,\n/static/fbdigitalads_mockup-51f8b282fbb3df95731d171247ebd4f0-6e031.jpg 1500w,\n/static/fbdigitalads_mockup-51f8b282fbb3df95731d171247ebd4f0-7b98f.jpg 2250w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 60.464396284829725%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"YorkUnite website splash\"\n        title=\"\"\n        src=\"/static/yorkunite_website-3760c604fa4bf6642001add7f220777d-755ae.jpg\"\n        srcset=\"/static/yorkunite_website-3760c604fa4bf6642001add7f220777d-f07fc.jpg 125w,\n/static/yorkunite_website-3760c604fa4bf6642001add7f220777d-9d06f.jpg 250w,\n/static/yorkunite_website-3760c604fa4bf6642001add7f220777d-755ae.jpg 500w,\n/static/yorkunite_website-3760c604fa4bf6642001add7f220777d-63ca3.jpg 750w,\n/static/yorkunite_website-3760c604fa4bf6642001add7f220777d-9b9a3.jpg 1000w,\n/static/yorkunite_website-3760c604fa4bf6642001add7f220777d-6e031.jpg 1500w,\n/static/yorkunite_website-3760c604fa4bf6642001add7f220777d-2538d.jpg 3230w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Lastly, I created a campaign video that is not only shareable, but it can also be broadcasted across the school’s existing television screens.</p>\n<p>youtube <a href=\"https://youtu.be/jRBvYFA4ooc\">https://youtu.be/jRBvYFA4ooc</a></p>\n<h1>Conclusion</h1>\n<p>The client particularly liked how this campaign can be implemented right away and at a low cost, since it utilizes already-existing resources.</p>\n<p>This project taught me how design can be used as a tool to build community. The project is united through a consistent and impactful visual brand that will stand out amongst current advertising in the university. Moreover, the strategy in place makes sure that the message is received by its intended audience. Overall, I believe the project can be successful in its goal of generating discussion on extreme weather preparedness within the community. </p>\n<p>For the full process documentation, view this case study on my website.</p>","frontmatter":{"title":"YorkUnite Campaign","graduate":"Charlotte Lucas","category":"Communication Design","category2":"Motion Design","category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.5,"src":"/static/yorkunite-c9c6656ad367504c0ac4ff28652d2813-0f681.png","srcSet":"/static/yorkunite-c9c6656ad367504c0ac4ff28652d2813-c1674.png 375w,\n/static/yorkunite-c9c6656ad367504c0ac4ff28652d2813-34f47.png 750w,\n/static/yorkunite-c9c6656ad367504c0ac4ff28652d2813-0f681.png 1500w,\n/static/yorkunite-c9c6656ad367504c0ac4ff28652d2813-606ea.png 2250w,\n/static/yorkunite-c9c6656ad367504c0ac4ff28652d2813-ce3b6.png 3000w,\n/static/yorkunite-c9c6656ad367504c0ac4ff28652d2813-5a49a.png 4001w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/yorkunite-campaign/","name":"/YorkUnite Campaign/"}}

